<template>
    <div>
        <el-row id="news" :gutter="20" v-masonry fit-width="true">
            <transition-group name="el-fade-in-linear">
                <el-col v-for="(item, index) in  displaydata " :lg="6" :md="8" :xl="4" :xs="24" :sm="12" :key="index"
                    v-masonry-tile ::key="index" class="mb-2 ">
                    <el-tooltip placement="auto" effect="light" v-if="item.local.type == '1'" trigger="click">
                        <template #content>
                            <span v-html="item.content"></span>
                        </template>
                        <el-countdown :value="item.local.countdown" prefix="还有:">
                            <template #title>
                                <img :src="item.banner">
                                <el-button class="addto" type="danger" @click="deletedata(index)" circle><el-icon>
                                        <Delete />
                                    </el-icon></el-button>
                                <span>{{
                                    item.title }}</span>
                            </template>
                        </el-countdown>
                    </el-tooltip>
                    <el-tooltip placement="auto" effect="light" v-if="item.local.type == '2'" trigger="click">
                        <template #content>
                            <span v-html="item.content"></span>
                        </template>
                        <div class="newsbg">
                            <img :src="item.banner">
                            <el-button class="addto" type="danger" @click="deletedata(index)" circle><el-icon>
                                    <Delete />
                                </el-icon></el-button>
                            <span>{{ item.title }}</span>
                        </div>
                    </el-tooltip>
                    <el-tooltip placement="auto" effect="light" v-if="item.local.type == '3'" trigger="click">
                        <template #content>
                            <span v-html="item.post.content"></span>
                            <p> <b><a :href="'https://www.miyoushe.com/' + item.local.game + '/article/' + item.post.post_id"
                                        target="_blank">
                                        前往页面</a></b></p>

                        </template>
                        <div class="newsbg">
                            <img :src="item.image_list.length > 0 ? item.image_list[0].url : item.post.cover">
                            <el-button class="addto" type="danger" @click="deletedata(index)" circle><el-icon>
                                    <Delete />
                                </el-icon></el-button>
                            <span>{{ item.post.subject }}</span>
                        </div>
                    </el-tooltip>
                    <el-tooltip placement="auto" effect="light" v-if="item.local.game == 'skland'" trigger="click">
                        <template #content>
                            <el-text v-for="(text, index)  in item.item.textSlice" :key="index">{{ text.c
                            }}
                                <br /></el-text>
                            <p> <b><a :href="item.url" target="_blank">
                                        前往页面</a></b></p>

                        </template>
                        <div class="newsbg">
                            <img :src="item.item.imageListSlice.length > 0 ? item.item.imageListSlice[0].url : ''">
                            <el-button class="addto" type="danger" @click="deletedata(index)" circle><el-icon>
                                    <Delete />
                                </el-icon></el-button>
                            <span>{{  item.item.title  }}</span>
                        </div>
                    </el-tooltip>
                </el-col>
            </transition-group>
            <el-col :span="24" style="height: 120px;">
            </el-col>
        </el-row>
    </div>
</template>

<script>


export default {
    name: 'GameInfoDynamicHome',

    data() {
        return {
            displaydata: []
        };
    },

    mounted() {
        this.display();
    },

    methods: {
        display() {
            if (localStorage.getItem('Homedata') != null) {
                this.displaydata = JSON.parse(localStorage.getItem('Homedata'))
                console.log(this.displaydata)
            }
        },
        deletedata(index) {
            this.displaydata.splice(index, 1);
            localStorage.setItem("Homedata", JSON.stringify(this.displaydata));
        }
    },
};
</script>

<style lang="scss" scoped>
#news {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
}

#news img {
    min-height: 60%;
    object-fit: cover;
}
</style>